<template>
	<div class="item" @click='fn()'>
		<span v-show='bol'><slot name='img'></slot></span>
		<span v-show='!bol'><slot name='img1'></slot></span>
		<p :class='{green:!bol}'>{{ txt }}</p>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:['txt','mark','id'],
		computed:{
			bol(){
				if(this.$parent.sel==this.mark){
					return false
				}
				return true
			}
		},
		methods:{
			fn(){
				this.$emit('change',this.mark)

				this.$router.push('/'+this.id)
			}
		}
	}
</script>

<style type="text/css">
	.item{width: 20%;float: left;background: #eee;text-align: center;height: 64px;}   
	.item_img{width: 30px;padding-top: 5px}
	.item .green{ color: #42bd56 }
</style>


