<template>
	<div class="HomeWrap">
		<div class="hello">
			<img src="../../assets/images/wx_20170605194925.jpg"/>
		</div>
		<MyHeader col='#42c055' class="homeHeader">
		<div slot='search' class="search">
			<img src="../../assets/images/ic_search.png">
			<input type="text" placeholder="影片 图片 唱片 小组等"></input>
			<img src="../../assets/images/ic_scan_gray.png">		
		</div>	
		<span slot='rtImg' class="rtImg"><img src="../../assets/images/ic_chat_white.png"></span>
		</MyHeader>
		<MyBanner swipeid='asd1'  :autoplay=1500 effect='fade' :bol=true>
			<div class="swiper-slide banner1" slot='swiper-con'><img src="../../assets/images/banner/01.jpg"></div>
			<div class="swiper-slide banner1" slot='swiper-con'><img src="../../assets/images/banner/02.jpg"></div>
			<div class="swiper-slide banner1" slot='swiper-con'><img src="../../assets/images/banner/03.jpg"></div>
		</MyBanner>
		<div class="Hot">
			<div class="Hot_t"><p><span>|</span>热点</p></div>
			<MyNews v-for='item in arr' :key='item.id' :arr='item'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='desc' class="home_p">{{ item.target.desc }}</p>
				<p slot='name'>作者:{{ item.target.author.name }}</p>
				<img :src="item.target.cover_url" slot='img'>
			</MyNews>	
		</div>
		<div class="tj">
			<div class="tj_t"><p><span>|</span>推荐</p></div>
			<MyNews v-for='item in arr1' :key='item.id' :arr='item'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='desc' class="home_p">{{ item.target.desc }}</p>
				<p slot='name'>作者:{{ item.target.author.name }}</p>
				<img :src="item.target.cover_url" slot='img'>
			</MyNews>			
		</div>
		<div class="yl">
			<div class="yl_t"><p><span>|</span>娱乐</p></div>
			<MyNews v-for='item in arr1' :key='item.id' :arr='item'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='desc' class="home_p">{{ item.target.desc }}</p>
				<p slot='name'>作者:{{ item.target.author.name }}</p>
				<img :src="item.target.cover_url" slot='img'>
			</MyNews>			
		</div>
		<div class="zhanwei"></div>	
	</div>	
</template>

<script type="text/javascript">
	import MyHeader from '../../components/Header'
	import MyBanner from '../../components/Banner'
	import MyNews from '../../components/News'

	export default{
		data(){
			return{
				arr:[],
				arr1:[],
				arr2:[]
			}
		},		
		components:{
			MyHeader,
			MyBanner,
			MyNews
		},
		mounted:function(){
			this.fn(),
			this.yc()
		},
		methods:{
			fn(){

				this.axios.get('static/data/homeData.json').then((response) => {
						          //console.log(response.data.data.recommend_feeds);
						          this.arr=response.data.recommend_feeds.slice(0,3)
						          this.arr1=response.data.recommend_feeds.slice(3,6)
						          this.arr2=response.data.recommend_feeds.slice(6,9)
						          console.log(this.arr)
						        })				
			},
			yc(){
				var IMG=document.getElementsByClassName('hello')[0];
				
				if( localStorage.getItem("collection") ){
					//IMG.style.display='none';
					this.run();
					
				}else{	
					console.log(12321321)
					var time=setTimeout(function(){
						IMG.style.display='none'
					},3000);
					var list={yc:true};
					localStorage.setItem( "collection", JSON.stringify(list) );
				}
				
			},
			run(){
				var IMG=document.getElementsByClassName('hello')[0];
				IMG.style.display='none';
			}
		},

	}
</script>

<style type="text/css">
	.homeHeader{width: 100%;height: 48px;border-bottom: 1px solid gray;line-height: 48px;position: fixed;z-index: 2}
	.zhanwei{height: 64px;}
	.HomeWrap{background: #eee}
	.search{width: 85%}
	.search input{height: 30px;border-radius: 5px;border: none;width: 100%;text-indent: 3em;margin-left: 10px}
	.search img:nth-of-type(1){width: 25px;position: absolute;top: 12px;left: 15px}
	.search img:nth-of-type(2){width: 25px;position: absolute;top: 12px;right: 55px}
	.rtImg img{width: 25px;position: absolute;top: 12px;right: 12px}
	.HomeWrap .Hot{width: 100%;}
	.HomeWrap .Hot_t p{color: orange;padding:10px 0;text-align: left;}
	.HomeWrap .Hot_t span{padding-right: 20px;font-weight:800;}
	.HomeWrap .tj{width: 100%;}
	.HomeWrap .tj_t p{color: skyblue;padding:10px 0;text-align: left;}
	.HomeWrap .tj_t span{padding-right: 20px;font-weight: 800}
	.HomeWrap .yl{width: 100%;}
	.HomeWrap .yl_t p{color: lightgreen;padding:10px 0;text-align: left;}
	.HomeWrap .yl_t span{padding-right: 20px;font-weight: 800}
	.banner1{margin-top: 49px;}
	.HomeWrap .Hot,.tj,.yl{margin-top: 20px;background-color: #fff;border-top: 1px solid #eee;box-shadow: 0 0 5px rgba(0,0,0,.5)}
	.HomeWrap .Hot{margin-top: -5px;}
	.HomeWrap .home_p{font-size:14px; overflow: hidden; color:#999;padding-top: 10px;width: 100%;display: -webkit-box;text-overflow: ellipsis;-webkit-line-clamp:3;-webkit-box-orient:vertical;}
	/*
		webkit-line-clamp:用来限制在一个块元素像是的文本的行数.为了实现该效果,它需要组合其他的
		display: -webkit-box; 必须结合的属性,将对象作为弹性伸缩盒子模型显示
		-webkit-box-orient:vertical;必须结合的属性,设置或检索伸缩盒子对象

	*/
	.HomeWrap .hello{
		height: 100%;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 10;
	}
	.HomeWrap .hello img{
		height: 100%;
		width: 100%;
	}
</style>


