<template>
	<div class="ds">
		<Film txt='新书速递'>
			<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
					<div v-for='item in arr' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
						<img :src="item.cover" class="IMG1"> 
						<span>{{ item.title }}</span><br/>
						<div class="score">					
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_half.png">
							<img src="../assets/images/rating_star_small_off.png">				
						</div>
						<span slot='pf' class="pf">评分{{ item.rate }}</span>
					</div>
			</MyBanner>				
		</Film>
		<MyNews>评分
			<div class="zixun"><p><span>|</span>图书资讯</p></div>
			<h3 slot='title'>作者谈作品|一座城，影响一代人</h3>
			<p slot='desc' class="ds_p">作者谈作品|一座城，影响一代人作者谈作品|一座城，影响一代人作者谈作品|一座城，影响一代人作者谈作品|一座城，影响一代人作者谈作品|一座城，影响一代人</p>
			<p slot='name'>作者:river</p>
			<img src="../assets/images/wx_20170605194925.jpg" slot='img'>
		</MyNews>
		<Film txt='最受关注图书'>
			<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
					<div v-for='item in arr1' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
						<img :src="item.cover" class="IMG1"> 
						<span>{{ item.title }}</span><br/>
						<div class="score">					
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_half.png">
							<img src="../assets/images/rating_star_small_off.png">				
						</div>
						<span slot='pf' class="pf">评分{{ item.rate }}</span>
					</div>
			</MyBanner>				
		</Film>
		<Film txt='最受期待图书'>
			<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
					<div v-for='item in arr2' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
						<img :src="item.cover" class="IMG1"> 
						<span>{{ item.title }}</span><br/>
						<div class="score">					
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_on.png">
							<img src="../assets/images/rating_star_small_half.png">
							<img src="../assets/images/rating_star_small_off.png">				
						</div>
						<span slot='pf' class="pf">评分{{ item.rate }}</span>
					</div>
			</MyBanner>				
		</Film>
		<div class="clear"></div>		
	</div>
</template>

<script type="text/javascript">
	import MyBanner from '../components/Banner'
	import Film from './Film'
	import MyNews from './News'
	export default{
		data(){
			return{
				arr:[
					// {'img':require('../assets/images/wx_20170605194925.jpg'),'title':'神奇女侠','pf':'评分:8.2',id:401},
					// {'img':require('../assets/dy/IMG_0380_05.jpg'),'title':'迷失Z城','pf':'评分:7.3',id:402},
					// {'img':require('../assets/dy/IMG_0380_07.jpg'),'title':'美好的意外','pf':'暂无评分',id:403},
					// {'img':require('../assets/dy/IMG_0384_03.jpg'),'title':'29+1','pf':'评分:7.4',id:404},
					// {'img':require('../assets/dy/IMG_0384_06.jpg'),'title':'29+1','pf':'评分:7.4',id:405},
					// {'img':require('../assets/dy/IMG_0384_08.jpg'),'title':'29+1','pf':'评分:7.4',id:406},
				],
				arr1:[],
				arr2:[]
			}
		},
		components:{
			MyBanner,
			Film,
			MyNews
		},
		mounted:function(){
			this.fn()
		},
		methods:{
			fn(){

				this.axios.get('static/data/banner.json').then((response) => {
						          console.log(response.data.subjects);
						          this.arr=response.data.subjects.slice(0,6)
						          this.arr1=response.data.subjects.slice(7,11)
						          this.arr2=response.data.subjects.slice(11,17)
						          //console.log(arr)
						        })				
			}			
		}
		// spaceBetween
	}
</script>


<style type="text/css">
	.ds{background: #eee;}
	.ds p span{color: #42bd56;float: right;padding-right: 15px;font-size: 14px;}
	.ds p i{padding-left: 10px}
	/*.dy .swiper-wrapper img{width:100%;margin:10px 10px;}*/
	.ds .swiper-wrapper{width: 100%;}
	.ds .swiper-wrapper .img1{width: 30px;}
	.ds .swiper-wrapper .img1 span{font-size: 14px;white-space: nowrap;}
	.ds .swiper-wrapper .img1 .pf{font-size: 12px;}
	.ds .swiper-wrapper .img1 span:nth-of-type(1){font-weight: 800}
	.ds .score img{width: 10px;height: 10px;float: left;padding-top: 5px;}
	.clear{height: 64px;}
	.ds .news{margin-top: 10px;border:none;}
	.ds .ds_p{font-size:14px; overflow: hidden; color:#999;padding-top: 10px;width: 100%;height: 60px;display: -webkit-box;text-overflow: ellipsis;-webkit-line-clamp:3;-webkit-box-orient:vertical;}
	.ds .IMG1{height: 148px;}
</style>



