<template>
	<div class="tabbar">
		<Warp :sel='selected'>
			<Item txt='首页' mark=1 id='home' @change='val'>
				<img src="../../assets/images/ic_tab_home_normal.png" slot='img' class="item_img">
				<img src="../../assets/images/ic_tab_home_active.png" slot='img1' class="item_img">
			</Item>
			<Item txt='书影音' mark=2 id='audio/Dy' @change='val'>
				<img src="../../assets/images/ic_tab_subject_normal.png" slot='img' class="item_img">
				<img src="../../assets/images/ic_tab_subject_active.png" slot='img1' class="item_img">
			</Item>
			<Item txt='广播' mark=3 id='broadcast' @change='val'>
				<img src="../../assets/images/ic_tab_status_normal.png" slot='img' class="item_img">
				<img src="../../assets/images/ic_tab_status_active.png" slot='img1' class="item_img">
			</Item>
			<Item txt='小组' mark=4 id='group' @change='val'>
				<img src="../../assets/images/ic_tab_group_normal.png" slot='img' class="item_img">
				<img src="../../assets/images/ic_tab_group_active.png" slot='img1' class="item_img">
			</Item>
			<Item txt='我的' mark=5 id='mine' @change='val'>
				<img src="../../assets/images/ic_tab_profile_female_normal.png" slot='img' class="item_img">
				<img src="../../assets/images/ic_tab_profile_female_active.png" slot='img1' class="item_img">
			</Item>					
		</Warp>
	</div>
</template>

<style type="text/css">
	
</style>

<script type="text/javascript">
import Warp from '../../components/Warp'
import Item from '../../components/Item'
	export default{
		components:{
			Warp,Item
		},
		data(){
			return{
				selected:1
			}
		},
		methods:{
			val(i){
				this.selected=i
			}
		}
	}
</script>


