<template>
	<div class="page3">
		<div class="title1">
			<img src="../assets/images/ic_status_anonymous_header.png">
			<h3>发现有趣的人</h3>
			<p>关注他们发现更大的世界</p>
		</div>
		<MyHp>
			<div class="lt" slot='lt'><img src="../assets/images/ic_change_round.png"><p>换一批</p></div>
			<span class="txt" slot='rt'>选好了，进入广播</span>
		</MyHp>
		<MyGz v-for='(item,index) in arr' :key='item.rs' :mark='item.mark'>
			<h4 slot='title'>{{ item.title }}</h4>
			<span slot='rs'>{{ item.rs }}人关注</span>
			<p slot='text'>{{ item.text }}</p>
			<img :src="item.img" slot='img'>
			<i  @click='fn(index)' slot='qx'>X</i>
		</MyGz>	
		<div class="g"></div>	
	</div>
</template>

<script type="text/javascript">
	import MyGz from '../components/Gz'
	import MyBanner from '../components/Banner'
	import MyHp from '../components/huanpi'
	export default{
		data(){
			return{
				arr:[
						{
							'title':'胡桑',
							'rs':4787,
							'text':'爱指向处于虚弱中的的他人。',
							'img':require("../assets/hsggx_d.jpg"),
							'mark':0
						},
						{
							'title':'胡桑',
							'rs':4787,
							'text':'爱指向处于虚弱中的的他人。',
							'img':require("../assets/hsggx_d.jpg"),
							'mark':1
						},
						{
							'title':'胡桑',
							'rs':4787,
							'text':'爱指向处于虚弱中的的他人。',
							'img':require("../assets/hsggx_d.jpg"),
							'mark':2
						},												
					]
			}
		},
		components:{
			MyGz,MyBanner,MyHp
		},
		methods:{
			fn(i){
				//console.log(1)
				this.arr.splice(i,1)
			}
		}
	}
</script>

<style type="text/css">
	.page3{background: #eee;margin: 0 auto;}
	.page3 .lt{ float: left; font-size: 14px;}
	.page3 .lt p{float: left;color: gray;padding-left: 5px}
	.page3 .lt img{width: 25px;padding-left: 20px;padding-top: 10px;float: left;}
	.page3 .txt{ background: #eee; font-size: 14px;color:#fff;width: 150px;height: 30px;display: inline-block;text-align: center;line-height: 30px;position: absolute;top: 5px;right: 20px;}
	.page3 .title1{width: 80%;height: 100px;margin: 0px auto;padding-top: 10px;}
	.page3 .title1 img{width: 100px;float: left;}
	.page3 .title1 h3{line-height: 20px;margin-top: 20px;}
	.page3 .title1 p{line-height: 20px;font-size: 14px;margin-top: 5px;}
	.page3 .g{height: 105px;}
</style>


