<template>
<div class="tv">
	<Film txt='今日更新'>
		<MyBanner swipeid='aaaaaa' :slidesPerView='1.5' :loop='false' slot='film'>
				<div v-for='item in arr' class="img2 swiper-slide" :key='item.id' slot='swiper-con'>
					<img :src="item.cover" class="IMG">
					<span>{{ item.title }}</span><br/>
					<div class="score">					
						<img src="../assets/images/rating_star_small_on.png">
						<img src="../assets/images/rating_star_small_on.png">
						<img src="../assets/images/rating_star_small_on.png">
						<img src="../assets/images/rating_star_small_half.png">
						<img src="../assets/images/rating_star_small_off.png">				
					</div><br/>
					<span slot='pf' class="pf">评分{{ item.rate }}</span>
				</div>
		</MyBanner>				
	</Film>
	<Film txt='近期值得看的国产剧'>
	<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
			<div v-for='item in arr1' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
				<img :src="item.cover" class="IMG1"> 
				<span>{{ item.title }}</span><br/>
				<div class="score">					
					<img src="../assets/images/rating_star_small_on.png">
					<img src="../assets/images/rating_star_small_on.png">
					<img src="../assets/images/rating_star_small_on.png">
					<img src="../assets/images/rating_star_small_half.png">
					<img src="../assets/images/rating_star_small_off.png">				
				</div>
				<span slot='pf' class="pf">评分{{ item.rate }}</span>
			</div>
	</MyBanner>				
</Film>
<Film txt='近期值得看的美剧'>
	<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
			<div v-for='item in arr2' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
				<img :src="item.cover" class="IMG1"> 
				<span>{{ item.title }}</span><br/>
				<div class="score">					
					<img src="../assets/images/rating_star_small_on.png">
					<img src="../assets/images/rating_star_small_on.png">
					<img src="../assets/images/rating_star_small_on.png">
					<img src="../assets/images/rating_star_small_half.png">
					<img src="../assets/images/rating_star_small_off.png">				
				</div>
				<span slot='pf' class="pf">评分{{ item.rate }}</span>
			</div>
	</MyBanner>				
</Film>
<div class="gaodu"></div>
</div>

</template>

<script type="text/javascript">
	import MyBanner from '../components/Banner'
	import Film from './Film'
	export default{
		data(){
			return{
				arr:[
					// {'img':require('../assets/images/wx_20170605194925.jpg'),'title':'神奇女侠','pf':'评分:8.2',id:401},
					// {'img':require('../assets/dy/IMG_0380_05.jpg'),'title':'迷失Z城','pf':'评分:7.3',id:402},
					// {'img':require('../assets/dy/IMG_0380_07.jpg'),'title':'美好的意外','pf':'暂无评分',id:403},
					// {'img':require('../assets/dy/IMG_0384_03.jpg'),'title':'29+1','pf':'评分:7.4',id:404},
					// {'img':require('../assets/dy/IMG_0384_06.jpg'),'title':'29+1','pf':'评分:7.4',id:405},
					// {'img':require('../assets/dy/IMG_0384_08.jpg'),'title':'29+1','pf':'评分:7.4',id:406},
				],
				arr1:[],
				arr2:[]
			}
		},
		components:{
			MyBanner,
			Film,
		},
		mounted:function(){
			this.fn()
		},
		methods:{
			fn(){

				this.axios.get('static/data/banner.json').then((response) => {
						          console.log(response.data.subjects);
						          this.arr=response.data.subjects.slice(7,11)
						          this.arr1=response.data.subjects.slice(11,17)
						          this.arr2=response.data.subjects.slice(18,22)
						          //console.log(arr)
						        })				
			}			
		}
		// spaceBetween
	}
</script>

<style type="text/css">
	.tv .swiper-wrapper .img2{margin: 0 10px;border: 1px solid gray;border-radius: 10px;box-shadow: 0 0 5px black;position: relative;}
	.tv .swiper-wrapper .img2 span{font-size: 14px;float: left;}
	.tv .swiper-wrapper .img2 .score{position: absolute;top: 60px;left: 113px;}
	.tv .swiper-wrapper .img2 .IMG{width:100px;padding: 5px;float: left;height: 153px;}
	.tv .swiper-wrapper .img1 span{font-size: 14px; text-overflow: ellipsis;}
	.tv .swiper-wrapper .img2 span{font-size: 14px; text-overflow: ellipsis;}
	.tv .swiper-wrapper .img2 .pf{font-size: 12px;}
	.tv .swiper-wrapper .img1 .pf{font-size: 12px;}
	.tv p span{color: #42bd56;float: right;padding-right: 15px;font-size: 14px;}
	.tv .score img{width: 10px;height: 10px;float: left;padding-top: 5px;}
	.tv .gaodu{height: 64px;}
	.tv .IMG1{height: 148px;}
	.tv .T1{height: 200px;}
	/*height: 200px;border: 1px solid black;*/
</style>



