<template>
	<div class="news" @click='fn()'>
		<div class="n_lt">
			<slot name='title'></slot>
			<!-- <h3>{{ txt }}</h3> -->
			<slot name='desc'></slot>
			<!-- <p>{{ text1 }}</p> -->
			<span>|</span>
			<slot name='name'></slot>
			<!-- <p>{{ text2 }}</p> -->
		</div>
		<slot name='img'></slot>
		<!-- <img :src="img"> -->
	</div>
</template>

<script type="text/javascript">
	export default{
		 props:['txt','img','text1','text2','arr'],
		 methods:{
		 	fn(){
		 		this.$router.push({
		 			path:'/News1',
		 			query:{
		 				name:this.arr
		 			}
		 		})
		 	}
		 }
	}
</script>

<style type="text/css">
	.n_lt{float: left;width: 195px;padding-left:10px;}
	.news{overflow:hidden; width: 100%;text-align: left;border-bottom: 1px solid #eee;padding-top: 10px;background: #fff;}
	.news h3{display: inline-block;}
	.news img{width: 100px;float: right;padding-right:10px;}
	.n_lt span{transform: rotate(90deg);display: inline-block;color: #eee}
	.news p{font-size: 12px;color:gray;line-height: 20px;}
	.news p:nth-of-type(2){padding-bottom: 10px;color:lightgray;}
</style>


