<template>
<div class="tc">
	<Film txt='热门活动'>
			<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
					<div v-for='item in arr' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
						<img :src="item.cover" class="IMG1"> 
						<span>{{ item.title }}</span><br/>
						<div class="score">					
											
						</div>
						<span slot='pf'>{{ item.pf }}</span>
						<i class="sp">官方售票</i>
					</div>
			</MyBanner>				
		</Film>
	<Film txt='官方售票'>
	<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
			<div v-for='item in arr1' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
				<img :src="item.cover" class="IMG1"> 
				<span>{{ item.title }}</span><br/>
				<div class="score">					
									
				</div>
				<span slot='pf'>{{ item.pf }}</span>
				<i class="sp">官方售票</i>
			</div>
	</MyBanner>				
</Film>
<Film txt='官方售票'>
	<MyBanner swipeid='asd2' :slidesPerView='3.5' :loop='false' :spaceBetween='10' slot='film'>
			<div v-for='item in arr2' class="img1 swiper-slide" :key='item.id' slot='swiper-con'>
				<img :src="item.cover" class="IMG1"> 
				<span>{{ item.title }}</span><br/>
				<div class="score">					
									
				</div>
				<span slot='pf'>{{ item.pf }}</span>
				<i class="sp">官方售票</i>
			</div>
	</MyBanner>				
</Film>
<div class="clear"></div>
</div>

</template>

<script type="text/javascript">
	import MyBanner from '../components/Banner'
	import Film from './Film'
	export default{
		data(){
			return{
				arr:[
					// {'img':require('../assets/images/wx_20170605194925.jpg'),'title':'创造空间','pf':'2017-06-10',id:401},
					// {'img':require('../assets/dy/IMG_0380_05.jpg'),'title':'诗与远方','pf':'2017-06-10',id:402},
					// {'img':require('../assets/dy/IMG_0380_07.jpg'),'title':'乌龙山伯爵','pf':'2017-06-10',id:403},
					// {'img':require('../assets/dy/IMG_0384_03.jpg'),'title':'白鹿原','pf':'2017-06-10',id:404},
					// {'img':require('../assets/dy/IMG_0384_06.jpg'),'title':'散打莎士比亚','pf':'2017-06-10',id:405},
					// {'img':require('../assets/dy/IMG_0384_08.jpg'),'title':'散打莎士比亚','pf':'2017-06-10',id:406},
				],
				arr1:[],
				arr2:[]
			}
		},
		components:{
			MyBanner,
			Film,
		},
		mounted:function(){
			this.fn()
		},
		methods:{
			fn(){

				this.axios.get('static/data/banner.json').then((response) => {
						          console.log(response.data.subjects);
						          this.arr=response.data.subjects.slice(5,11)
						          this.arr1=response.data.subjects.slice(22,27)
						          this.arr2=response.data.subjects.slice(29,35)
						          //console.log(arr)
						        })				
			}			
		}
		// spaceBetween
	}
</script>

<style type="text/css">
	.tc{background: #eee;}
	.tc p span{color: #42bd56;float: right;padding-right: 15px;font-size: 14px;}
	.tc p i{padding-left: 10px}
	/*.dy .swiper-wrapper img{width:100%;margin:10px 10px;}*/
	.tc .swiper-wrapper{width: 100%;}
	.tc .swiper-wrapper .img1{width: 30px;}
	.tc .swiper-wrapper .img1 span{font-size: 14px;}
	.tc .swiper-wrapper .img1 span:nth-of-type(1){font-weight: 800}
	.tc .sp{display: inline-block;width: 60px;height: 15px;border: 1px solid darkred;color: darkred;font-size: 12px;border-radius: 5px;font-style: normal;font-family: '宋体'}
	.tc .IMG1{height: 148px;}
</style>


