<template>
	<div class="xiaozu">
		<div class="geduan" v-for='item in arr'>				
		    <img :src="item.url" class="leftfd">
		   	<div class="text"> 
		   		<h4 class="ltwen1">{{ item.txt }}</h4><br>
		   		<img src="../assets/images/ic_user_info_gender.png" class="touxiang">
				<p class="ltwen2">{{ item.txt1 }}</p>
		   	</div>
			<div class="Right">
				<p class="rtwen">{{ item.rs  }}人</p>
				<img src="../assets/images/ic_group_check_anonymous.png" class="gou">
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:['arr']
	}
</script>

<style type="text/css">
	.xiaozu .text{width: 200px;height: 55px;margin-left: 40px;overflow: hidden;text-overflow: ellipsis;}
	.xiaozu .Right{position: absolute;top:20px;right:0px;}
	.xiaozu .ys{width: 28px;height: 20px;font-size: 14px;background:#fff;position: absolute;top:-10px;left:46%;text-align: center;}
	.xiaozu .geduan{padding-bottom: 15px;position: relative;}
	.xiaozu .clear{clear: both;}
	.xiaozu .rtwen{float: left;display: inline-block;margin-top: 5px;font-size: 12px;color:#999;margin-right: 15px;margin-top: -10px;}
	.xiaozu .ltwen1{float: left;display: inline-block;margin-left: 8px;font-size: 14px;overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;}
	.xiaozu .ltwen2{float: left;display: inline-block;margin-left: 8px;font-size: 12px;color:#999;
	overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;
	}
	.xiaozu .touxiang{width: 40px;position: absolute;top: 20px;left: 0;}
	.xiaozu .gou{float: left;display: inline-block;width: 15px;margin-top: -8px;}	
</style>



